import { gsap, Power2 } from 'gsap'

const chunk = function (arr) {
  const half = Math.floor(arr.length / 2)
  return arr.slice(0, half)
}
const chunk2 = function (arr) {
  const half = Math.floor(arr.length / 2)
  return arr.slice(half, arr.length)
}

export default {
  props: {
    story: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      proyectos: chunk(this.story.content.proyectos),
      proyectos2: chunk2(this.story.content.proyectos),
      transY1: [-4, 8],
      transY2: [-8, 4],
      scale: [0.6, 1.1],
    }
  },
  mounted() {
    this.$Lazyload.$on(
      'loading',
      (
        {
          bindType,
          el,
          naturalHeight,
          naturalWidth,
          $parent,
          src,
          loading,
          error,
        },
        formCache
      ) => {
        if (el.classList.contains('gallery')) {
          gsap.set(el, {
            clipPath: 'inset(0% 0% 100% 0%)',
            autoAlpha: 0,
          })
        }
      }
    )
    this.$Lazyload.$on(
      'loaded',
      (
        {
          bindType,
          el,
          naturalHeight,
          naturalWidth,
          $parent,
          src,
          loading,
          error,
        },
        formCache
      ) => {
        if (window) {
          if (el.classList.contains('gallery')) {
            gsap.to(el, {
              clipPath: 'inset(0% 0% 0% 0%)',
              autoAlpha: 1,
              duration: 0.5,
              ease: Power2.easeInOut,
              onStart: () => {
                window.dispatchEvent(new Event('resize'))
              },
            })
          }
        }
      }
    )
    gsap.set('.marquee_int img', {
      y: () => {
        const sc = (
          Math.random() * (this.transY1[1] - this.transY1[0]) +
          this.transY1[0]
        ).toFixed(4)
        return sc
      },
      scale: () => {
        const sc = (
          Math.random() * (this.scale[1] - this.scale[0]) +
          this.scale[0]
        ).toFixed(4)
        return sc
      },
    })
    gsap.set('.marquee_int2 img', {
      y: () => {
        const sc = (
          Math.random() * (this.transY2[1] - this.transY2[0]) +
          this.transY2[0]
        ).toFixed(4)
        return sc
      },
      scale: () => {
        const sc = (
          Math.random() * (this.scale[1] - this.scale[0]) +
          this.scale[0]
        ).toFixed(4)
        return sc
      },
    })
    gsap.set('.text_home_int .anim', {
      clipPath: 'inset(0% 0% 100% 0%)',
      y: '5vh',
    })

    gsap.set('.marquee_int2, .marquee_int', {
      y: '-5vh',
      autoAlpha: 0,
    })
    gsap.to('.text_home_int .anim', {
      clipPath: 'inset(0% 0% 0% 0%)',
      delay: 3,
      duration: 0.8,
      stagger: 0.2,
      y: 0,
      ease: Power2.easeInOut,
    })
    gsap.to('.bu', {
      '--font-weight': 500,
      duration: 0.8,
      delay: 3.2,
      ease: Power2.easeInOut,
    })
    gsap.to('.marquee_int2, .marquee_int', {
      duration: 0.4,
      stagger: 0.2,
      y: 0,
      autoAlpha: 1,
      delay: 2,
      ease: Power2.easeInOut,
    })
    // this.$store.subscribe((mutation, state) => {
    //   if (
    //     mutation.type === 'app/setPreload' ||
    //     mutation.type === 'app/setSection'
    //   ) {

    //   }
    // })
  },
  methods: {
    toScrollNav(event) {
      this.$emit('to-scroll-nav', event)
    },
    loaded(e) {
      if (window) {
        if (
          !e.target.classList.contains('isLoaded') &&
          !e.target.classList.contains('isLoading')
        ) {
          gsap.set(e.target, {
            autoAlpha: 0,
          })
        } else {
          gsap.to(e.target, {
            autoAlpha: 1,
            duration: 0.5,
            ease: Power2.easeInOut,
            onComplete: () => {
              window.dispatchEvent(new Event('resize'))
            },
          })
        }
      }
    },
  },
}
